.yes {
  margin: 0 auto;
  display: flex;
  max-width: 1373px;
  justify-content: space-between;
  margin-top: 233px;
  align-items: center;
  padding-right: 15px;
  padding-left: 15px;
  animation: yesEffect 2s;
  animation-iteration-count: 1;
}
@keyframes yesEffect {
  0% { transform: translate(0, -300px); }
  100% { transform: translate(0, 0px); }
}
.yes__text {
  color: #000;
  text-align: center;
  font-family: "Nyasha Sans";
  font-size: 40px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
.yes__circle {
  background-color: #000;
  width: 28px;
  height: 28px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.yes__circle_inner {
  width: 17px;
  height: 17px;
  border-radius: 50%;
  background-color: #F7D046;
}


.title {
  color: #000;
  text-align: center;
  text-shadow: -10.863px 10.863px 0px #FFD703;
  font-family: "Cy Grotesk";
  font-size: 130px;
  font-style: normal;
  font-weight: 800;
  line-height: normal;
  text-transform: uppercase;
  margin-top: 20px;
  animation: titleEffect 5s;
  animation-iteration-count: 1;
}
@keyframes titleEffect {
  0% { transform: translate(-500px, 0); }
  50% { transform: translate(300px, 0); }
  100% { transform: translate(0, 0); }
}

@media (max-width: 1264px) {
  .yes__text {
    font-size: 30px;
  }
}

@media (max-width: 889px) {
  .title {
    font-size: 70px;
  }
  .yes__circle {
    margin-top: 20px;
    margin-bottom: 20px;
  }
  .yes {
    margin-top: 150px;
    flex-direction: column;
  } 
}
@media (max-width: 488px) {
  .title {
    font-size: 50px;
  }
}

.post {
  overflow: hidden;
  max-width: 1373px;
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  margin-top: 80px;
  display: flex;
  justify-content: space-between; 
  align-items: flex-start;
  padding-left: 15px;
  padding-right: 15px;
  position: relative;
}

.post__link {
  display:block;
  padding: 10.491px 29.141px;
  color: #000;
  text-align: center;
  font-family: "Nyasha Sans";
  font-size: 48px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  position: relative;
}
.post__link:before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #FFD703;
  transform: skew(20deg);
  z-index: -1;
}
.post__links {
  display: flex;
  flex-wrap: wrap;
  max-width: 725px;
  gap: 16px;
  justify-content: center;
}

.post__cat {
  position: absolute;
  width: 300px;
  height: auto;
  left: 65%;
  top: 30%;
  transform: rotate(-20deg);
  animation: postEffect 3s;
  animation-iteration-count: infinite;
}
@keyframes postEffect {
  0% { transform: translate(5px, -6px) rotate(-24deg); }
  20% { transform: translate(-7px, 4px) rotate(-16deg); }
  40% { transform: translate(5px, -7px) rotate(-17deg); }
  60% { transform: translate(-7px, 5px) rotate(-23deg); }
  80% { transform: translate(-5px, -5px) rotate(-16deg); }
  100% { transform: translate(5px, -6px) rotate(-24deg); }
}
@media (max-width: 1316px) {
  .post {
    flex-direction: column;
    align-items: center;
  }
  .post__links {
    margin-top: 700px;
  }
  .post__cat {
    left: auto;
    right: auto;
    top: 53%;
    transform: rotate(-20deg);
  }
  .post__img {
    max-width: 100%;
    height: auto;
  }
}
@media (max-width: 599px) {
  .post__cat {
    top: 48%;
  }
}
@media (max-width: 533px) {
  .post__cat {
    top: 44%;
  }
}
@media (max-width: 438px) {
  .post__cat {
    top: 36%;
  }
}
.looking {
  max-width: 1373px;
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  padding-left: 30px;
  padding-right: 30px;
  margin-top: 359px;
  position: relative;
}

.looking__title {
  max-width: 937px;
  width: 100%;
  color: #000;
  text-shadow: -10.863px 10.863px 0px #F7D046;
  font-family: "Cy Grotesk";
  font-size: 86px;
  font-style: normal;
  font-weight: 800;
  line-height: 140%; /* 120.4px */
  text-transform: uppercase;
  animation: titleEffect 5s;
  animation-iteration-count: 1;
}
/* here */
@keyframes lookingTitle {
  0% { transform: translate(-500px, 0); }
  50% { transform: translate(300px, 0); }
