.looking__link {
  display:block;
  padding: 10.491px 29.141px;
  color: #000;
  text-align: center;
  font-family: "Nyasha Sans";
  font-size: 48px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  position: relative;
  width: max-content;
  margin-top: 65px;
}
.looking__link:before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
